import {
  DefaultPortModel,
  DefaultPortModelOptions,
} from '@projectstorm/react-diagrams';

export class AdvancedPortModel extends DefaultPortModel {
  channelName: string;

  constructor(options: DefaultPortModelOptions & { channelName: string }) {
    super({ ...options });
    this.channelName = options.channelName;

    if (options.in) {
      this.options.locked = true;
    }
  }

  canLinkToPort = (port: AdvancedPortModel): boolean => {
    return (
      this.options.in !== port.getOptions().in &&
      this.channelName === port.channelName
    );
  };
}
