import createEngine, {
  DefaultLinkModel,
  DiagramModel,
  DiagramModelGenerics,
  DiagramEngine,
  NodeModel,
} from '@projectstorm/react-diagrams';
import { CustomNodeModel } from './node-widget/NodeModel';
import { NodeFactory } from './node-widget/NodeFactory';
import { TopologyElement } from '../../models/TopologyElement.model';

export class Application {
  engine = createEngine();
  model: DiagramModel<DiagramModelGenerics>;

  constructor() {
    this.engine.getNodeFactories().registerFactory(new NodeFactory());
    CustomNodeModel.deviceCounter = 0;
    this.model = new DiagramModel();
    this.engine.setModel(this.model);
  }

  public newModel = () => {
    this.model = new DiagramModel();
    CustomNodeModel.deviceCounter = 0;
    this.engine.setModel(this.model);
  };

  public serializeDiagram = () => {
    this.model.getNodeLayers();
  };

  public deserializeDiagram = (serializedModel: string) => {
    this.model.deserializeModel(JSON.parse(serializedModel), this.engine);
    this.engine.setModel(this.model);
  };

  public prepareDiagramToAnalyse = (): TopologyElement[] => {
    const serializedDiagram = this.model.getNodeLayers()[0];
    const models = serializedDiagram.getModels();
    const links = serializedDiagram.getParent().getLayers()[0].getModels();

    let preparedDiagram: TopologyElement[] = [];

    Object.keys(models).forEach((v) => {
      const model = models[v] as CustomNodeModel;

      preparedDiagram.push({
        deviceId: model.deviceId,
        deviceType: model.deviceType,
        connectedOutDevicesIds: [],
      });
    });

    Object.keys(links).forEach((v) => {
      const link = links[v] as DefaultLinkModel;

      const sourcePort = link.getSourcePort().getOptions().name.split('^');
      const targetPort = link.getTargetPort().getOptions().name.split('^');

      const sourceNode = preparedDiagram.find(
        (v) => v.deviceId === parseInt(sourcePort[0])
      );

      if (sourceNode) {
        sourceNode.connectedOutDevicesIds.push({
          deviceId: parseInt(targetPort[0]),
          channelName: targetPort[1],
        });
      }
    });

    return preparedDiagram;
  };

  public getActiveDiagram = (): DiagramModel<DiagramModelGenerics> => {
    return this.model;
  };

  public getDiagramEngine = (): DiagramEngine => {
    return this.engine;
  };
}
