import React from 'react';
import { IoIosAdd, IoIosSave, IoIosTrash } from 'react-icons/io';
//styles
import './Simulation.css';
//mobx
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
//components
import Input from '../../components/Input'; //models
import { Version } from '../../models/Version.model';
import VersionListElement from '../../components/VersionListElement';
import Process from '../../components/process/Process';
import { Process as ProcessModel } from '../../models/Version.model';
import Tooltip from '../../components/tooltip/Tooltip';
import TranslationService from '../../services/TranslationService';

interface IProps {
  store?: RootStore;
}

@inject('store')
@observer
class Versions extends React.Component<IProps> {
  nameInputRef = React.createRef<Input>();
  processesRefs: React.RefObject<Process>[] = [];

  setDataToForm = () => {
    const { activeVersion, versions } = this.props.store!.newSimulationStore;

    if (activeVersion !== undefined) {
      const version = versions[activeVersion];

      this.nameInputRef.current?.setValue(version.name);

      version.processes.forEach((v, index) => {
        this.processesRefs[index].current?.setValue(v);
      });
    }
  };

  saveData = () => {
    const {
      activeVersion,
      editVersion,
      changeActiveVersion,
      checkVersionNameUniqueness,
    } = this.props.store!.newSimulationStore;

    if (activeVersion !== undefined) {
      const validations = [
        this.nameInputRef.current?.validate(),
        ...this.processesRefs.map((v, index) => v.current?.validate(index)),
      ];

      if (validations.findIndex((v) => !v) === -1) {
        let processes: ProcessModel[] = [];

        this.processesRefs.forEach((v) => {
          v.current && processes.push(v.current.getValue());
        });

        const updatedVersion: Version = {
          name: this.nameInputRef.current?.getValue() || '',
          processes,
        };

        if (!checkVersionNameUniqueness(updatedVersion.name)) {
          this.nameInputRef.current?.setValidity(
            true,
            TranslationService.unique_name
          );
          return false;
        }

        editVersion(updatedVersion, activeVersion);
        changeActiveVersion(undefined);
        return true;
      } else {
        return false;
      }
    }

    return true;
  };

  changeVersion = (id: number) => {
    const {
      versions,
      changeActiveVersion,
    } = this.props.store!.newSimulationStore;

    this.processesRefs = [];
    this.processesRefs = versions[id].processes.map((v) =>
      React.createRef<Process>()
    );

    changeActiveVersion(id);
    setTimeout(() => {
      this.setDataToForm();
    }, 100);
  };

  addVersion = () => {
    const addVersionFunc = this.props.store!.newSimulationStore.addVersion;
    const { versions } = this.props.store!.newSimulationStore;

    addVersionFunc();
    setTimeout(() => {
      this.processesRefs = versions[versions.length - 1].processes.map((v) =>
        React.createRef<Process>()
      );
      this.setDataToForm();
    }, 100);
  };

  addProcessToVersion = () => {
    const {
      addProcessToVersion,
      activeVersion,
    } = this.props.store!.newSimulationStore;

    if (activeVersion !== undefined) {
      this.processesRefs.push(React.createRef<Process>());
      addProcessToVersion(activeVersion);
    }
  };

  removeProcessFromVersion = (index: number) => {
    const {
      activeVersion,
      removeProcessFromVersion,
    } = this.props.store!.newSimulationStore;

    if (activeVersion !== undefined) {
      removeProcessFromVersion(activeVersion, index);
      setTimeout(() => {
        this.processesRefs.splice(index, 1);
      }, 100);
    }
  };

  render() {
    const {
      versions,
      activeVersion,
      removeVersion,
    } = this.props.store!.newSimulationStore;

    return (
      <div className="simulation-padding-container">
        <div className="simulation-flex-container">
          <div className="header-container">
            <div className="header-inside-container">
              <h2>{TranslationService.versions}</h2>
              <Tooltip
                type="small"
                placement="bottom"
                title={TranslationService.versions_and_proccesses}
                content={TranslationService.versions_and_proccesses_tooltip}
              />
            </div>
            <div className="buttons-container">
              <div
                onClick={this.addVersion}
                className="round-button round-button-primary"
              >
                <IoIosAdd />
                <p>{TranslationService.add_version}</p>
              </div>
            </div>
          </div>

          <div className="section ">
            <div className="medium-list">
              {versions.map((element: Version, index: number) => (
                <VersionListElement
                  key={'' + index}
                  active={activeVersion === index}
                  onPress={() => this.changeVersion(index)}
                  version={element}
                />
              ))}
            </div>
          </div>
        </div>

        <div
          className={
            'simulation-flex-container' +
            (activeVersion === undefined
              ? ' simulation-flex-container-removed'
              : '')
          }
        >
          {activeVersion !== undefined && (
            <>
              <div className="header-container">
                <h2>{TranslationService.version_details}</h2>
                <div className="buttons-container">
                  <div
                    onClick={() => removeVersion(activeVersion)}
                    className="round-button round-button-secondary round-button-remove"
                  >
                    <IoIosTrash />
                  </div>

                  <div
                    onClick={this.saveData}
                    className="round-button round-button-primary"
                  >
                    <IoIosSave />
                    <p>{TranslationService.save}</p>
                  </div>
                </div>
              </div>

              <div className="section">
                <div className="label-inside-container">
                  <label>{TranslationService.version_name}</label>
                  <Tooltip
                    type="small"
                    placement="bottom"
                    title={TranslationService.version_name}
                    content={TranslationService.version_name_tooltip}
                  />
                </div>
                <Input ref={this.nameInputRef} white type="elementName" />

                <div className="header-container">
                  <h3>{TranslationService.processes}</h3>
                  <div className="buttons-container">
                    <div
                      onClick={this.addProcessToVersion}
                      className="round-button round-button-primary"
                    >
                      <IoIosAdd />
                      <p>{TranslationService.add_process}</p>
                    </div>
                  </div>
                </div>

                {versions[activeVersion] &&
                  versions[activeVersion].processes.map((process, index) => {
                    return (
                      <Process
                        key={'channel' + index}
                        ref={this.processesRefs[index]}
                        process={process}
                        removeProcess={() =>
                          this.removeProcessFromVersion(index)
                        }
                      />
                    );
                  })}
              </div>
            </>
          )}
        </div>

        {versions.length === 0 && (
          <div className="no-content-container">
            <p>{TranslationService.no_version_added_header}</p>
            <p>{TranslationService.no_version_added_description}</p>
          </div>
        )}
      </div>
    );
  }
}

export default Versions;
