import React from 'react';
import { IoIosAdd, IoIosSave, IoIosTrash } from 'react-icons/io';
//styles
import './Simulation.css';
//mobx
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
//components
import Input from '../../components/Input';
import Tooltip from '../../components/tooltip/Tooltip';
//@ts-ignore
import NumberInput from '../../components/number-input/NumberInput';
import Checkbox from '../../components/checkbox/Checkbox';
import MediumListElement from '../../components/MediumListElement';
//models
import {
  Medium as MediumModel,
  Channel as ChannelModel,
} from '../../models/Medium.model';
import TranslationService from '../../services/TranslationService';

interface IProps {
  store?: RootStore;
}

interface ChannelRef {
  inputRef: React.RefObject<Input>;
  bufferRef: React.RefObject<NumberInput>;
  checkboxRef: React.RefObject<Checkbox>;
}

@inject('store')
@observer
class MediumChannels extends React.Component<IProps> {
  channels: ChannelRef[] = [0, 1, 2].map((v) => ({
    inputRef: React.createRef<Input>(),
    bufferRef: React.createRef<NumberInput>(),
    checkboxRef: React.createRef<Checkbox>(),
  }));

  nameInputRef = React.createRef<Input>();
  refDefQuality = React.createRef<NumberInput>();
  refDefListCurrent = React.createRef<NumberInput>();
  refDefSenCurrent = React.createRef<NumberInput>();
  refDefRecCurrent = React.createRef<NumberInput>();

  setDataToForm = () => {
    const { activeMedium, mediums } = this.props.store!.newSimulationStore;

    if (activeMedium !== undefined) {
      const medium = mediums[activeMedium];

      const nameInputRef = this.nameInputRef.current;
      const refDefQuality = this.refDefQuality.current;
      const refDefListCurrent = this.refDefListCurrent.current;
      const refDefSenCurrent = this.refDefSenCurrent.current;
      const refDefRecCurrent = this.refDefRecCurrent.current;

      if (
        refDefQuality &&
        refDefListCurrent &&
        refDefSenCurrent &&
        refDefRecCurrent &&
        nameInputRef
      ) {
        refDefQuality.setValue(medium.defaultQuality);
        refDefListCurrent.setValue(medium.defaultListeningCurrent);
        refDefSenCurrent.setValue(medium.defaultSendingCurrent);
        refDefRecCurrent.setValue(medium.defaultReceivingCurrent);
        nameInputRef.setValue(medium.name);
      }
    }
  };

  saveData = () => {
    const {
      activeMedium,
      editMedium,
      changeActiveMedium,
      checkMediumNameUniqueness,
      checkChannelNameUniqueness,
    } = this.props.store!.newSimulationStore;

    if (activeMedium !== undefined) {
      const nameInputRef = this.nameInputRef.current;
      const refDefQuality = this.refDefQuality.current;
      const refDefListCurrent = this.refDefListCurrent.current;
      const refDefSenCurrent = this.refDefSenCurrent.current;
      const refDefRecCurrent = this.refDefRecCurrent.current;

      if (
        refDefQuality &&
        refDefListCurrent &&
        refDefSenCurrent &&
        refDefRecCurrent &&
        nameInputRef
      ) {
        const nameValidity = nameInputRef.validate();

        if (nameValidity) {
          let channels: ChannelModel[] = [];
          let channelsValidity = true;

          this.channels.forEach((channel, index) => {
            const nameRef = channel.inputRef.current;
            const checkboxRef = channel.checkboxRef.current;
            const bufferRef = channel.bufferRef.current;

            if (nameRef && checkboxRef && bufferRef) {
              if (channelsValidity) {
                channelsValidity = nameRef.validate() || false;

                if (!checkChannelNameUniqueness(nameRef.getValue(), index)) {
                  channelsValidity = false;
                  nameRef.setValidity(true, "The name isn't unique.");
                }

                const isInfinityBuffer = checkboxRef.getValue();
                channels.push({
                  name: nameRef.getValue(),
                  buffer: isInfinityBuffer ? 'infinity' : bufferRef.getValue(),
                });
              }
            }
          });

          if (!channelsValidity) {
            return false;
          }

          const updatedMedium: MediumModel = {
            name: nameInputRef.getValue(),
            defaultQuality: refDefQuality.getValue(),
            defaultListeningCurrent: refDefListCurrent.getValue(),
            defaultSendingCurrent: refDefSenCurrent.getValue(),
            defaultReceivingCurrent: refDefRecCurrent.getValue(),
            channels: channels,
          };

          if (!checkMediumNameUniqueness(updatedMedium.name)) {
            nameInputRef.setValidity(true, "The name isn't unique.");
            return false;
          }

          editMedium(updatedMedium, activeMedium);
          changeActiveMedium(undefined);
          return true;
        } else {
          return false;
        }
      }
    }
    return true;
  };

  changeMedium = (id: number) => {
    const { changeActiveMedium } = this.props.store!.newSimulationStore;

    changeActiveMedium(id);
    setTimeout(() => {
      this.setDataToForm();
    }, 100);
  };

  addMedium = () => {
    const { addMedium } = this.props.store!.newSimulationStore;

    addMedium();
    setTimeout(() => {
      this.setDataToForm();
    }, 100);
  };

  addChannel = (mediumId: number) => {
    const { addChannelToMedium } = this.props.store!.newSimulationStore;

    this.channels.push({
      inputRef: React.createRef<Input>(),
      bufferRef: React.createRef<NumberInput>(),
      checkboxRef: React.createRef<Checkbox>(),
    });

    addChannelToMedium(mediumId);
  };

  render() {
    const {
      mediums,
      activeMedium,
      removeMedium,
      removeChannelFromMedium,
    } = this.props.store!.newSimulationStore;

    return (
      <div className="simulation-padding-container">
        <div className="simulation-flex-container">
          <div className="header-container">
            <div className="header-inside-container">
              <h2>{TranslationService.mediums}</h2>
              <Tooltip
                type="small"
                placement="bottom"
                title={TranslationService.mediums}
                content={TranslationService.mediums_tooltip}
              />
            </div>
            <div className="buttons-container">
              <div
                onClick={this.addMedium}
                className="round-button round-button-primary"
              >
                <IoIosAdd />
                <p>{TranslationService.add_medium}</p>
              </div>
            </div>
          </div>

          <div className="section ">
            <div className="medium-list">
              {mediums.map((element: MediumModel, index: number) => (
                <MediumListElement
                  key={'' + index}
                  active={activeMedium === index}
                  onPress={() => this.changeMedium(index)}
                  medium={element}
                />
              ))}
            </div>
          </div>
        </div>

        <div
          className={
            'simulation-flex-container' +
            (activeMedium === undefined
              ? ' simulation-flex-container-removed'
              : '')
          }
        >
          {activeMedium !== undefined && (
            <>
              <div className="header-container">
                <h2>{TranslationService.medium_details}</h2>
                <div className="buttons-container">
                  <div
                    onClick={() => removeMedium(activeMedium)}
                    className="round-button round-button-secondary round-button-remove"
                  >
                    <IoIosTrash />
                  </div>

                  <div
                    onClick={this.saveData}
                    className="round-button round-button-primary"
                  >
                    <IoIosSave />
                    <p>{TranslationService.save}</p>
                  </div>
                </div>
              </div>

              <div className="section">
                <h3>{TranslationService.primary}</h3>

                <div className="label-inside-container">
                  <label>{TranslationService.medium_name}</label>
                  <Tooltip
                    type="small"
                    placement="bottom"
                    title={TranslationService.medium_name}
                    content={TranslationService.medium_name_tooltip}
                  />
                </div>
                <Input ref={this.nameInputRef} white type="elementName" />

                <div className="label-inside-container label-inside-container-with-bottom">
                  <h3>{TranslationService.constants}</h3>
                  <Tooltip
                    type="small"
                    placement="bottom"
                    title={TranslationService.constants}
                    content={TranslationService.medium_constants_tooltip}
                  />
                </div>
                <div className="medium-constants-row">
                  <div className="medium-constants-column">
                    <label>{TranslationService.default_quality}</label>
                    <NumberInput ref={this.refDefQuality} />
                  </div>
                  <div className="medium-constants-column">
                    <label>
                      {TranslationService.default_listening_current}
                    </label>
                    <NumberInput ref={this.refDefListCurrent} />
                  </div>
                </div>

                <div className="medium-constants-row">
                  <div className="medium-constants-column">
                    <label>{TranslationService.default_sending_current}</label>
                    <NumberInput ref={this.refDefSenCurrent} />
                  </div>
                  <div className="medium-constants-column">
                    <label>
                      {TranslationService.default_recieving_current}
                    </label>
                    <NumberInput ref={this.refDefRecCurrent} />
                  </div>
                </div>

                <div className="header-container">
                  <div className="header-inside-container">
                    <h3>{TranslationService.channels}</h3>
                    <Tooltip
                      type="small"
                      placement="bottom"
                      title={TranslationService.channels}
                      content={TranslationService.channels_tooltip}
                    />
                  </div>
                  <div className="buttons-container">
                    {mediums[activeMedium] &&
                      mediums[activeMedium].channels.length < 3 && (
                        <div
                          onClick={() => this.addChannel(activeMedium)}
                          className="round-button round-button-primary"
                        >
                          <IoIosAdd />
                          <p>{TranslationService.add_channel}</p>
                        </div>
                      )}
                  </div>
                </div>

                {mediums[activeMedium] &&
                  mediums[activeMedium].channels.map((channel, index) => {
                    return (
                      <div key={'channel' + index} className="section">
                        <div className="label-inside-container">
                          <label>{TranslationService.channel_name}</label>
                          <Tooltip
                            type="small"
                            placement="bottom"
                            title={TranslationService.channel_name}
                            content={TranslationService.channel_name_tooltip}
                          />
                        </div>
                        <div className="channel-name-container">
                          <div className="delete-input-conatiner">
                            <Input
                              ref={this.channels[index].inputRef}
                              initialValue={channel.name}
                              white
                              type="elementName"
                            />
                          </div>
                          <div
                            onClick={() =>
                              removeChannelFromMedium(activeMedium, index)
                            }
                            className="round-button round-button-secondary round-button-remove"
                          >
                            <IoIosTrash />
                          </div>
                        </div>

                        <div className="label-inside-container">
                          <label>{TranslationService.buffer_name}</label>
                          <Tooltip
                            type="small"
                            placement="bottom"
                            title={TranslationService.buffer_name}
                            content={TranslationService.channel_buffer_tooltip}
                          />
                        </div>
                        <NumberInput
                          ref={this.channels[index].bufferRef}
                          initialValue={
                            channel.buffer === 'infinity' ? 1.0 : channel.buffer
                          }
                        />
                        <Checkbox
                          ref={this.channels[index].checkboxRef}
                          placeholder={TranslationService.is_infinity}
                          initialValue={channel.buffer === 'infinity'}
                        />
                        <div className="separator" />
                      </div>
                    );
                  })}
              </div>
            </>
          )}
        </div>

        {mediums.length === 0 && (
          <div className="no-content-container">
            <p>{TranslationService.no_medium_added_header}</p>
            <p>{TranslationService.no_medium_added_description}</p>
          </div>
        )}
      </div>
    );
  }
}

export default MediumChannels;
