import React from 'react';
import { withRouter } from 'react-router-dom';
//styles
import './Simulation.css';
//mobx
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
//components
import { IoIosPlay, IoIosThumbsDown } from 'react-icons/io';
import { classNames } from '../../utils/ClassNames';
//@ts-ignore
import Loader from 'react-loader-spinner';
import TranslationService from '../../services/TranslationService';
interface IProps {
  store?: RootStore;
  history: any;
  location: any;
  match: any;
}

@inject('store')
@observer
class Start extends React.Component<IProps> {
  state = {
    simulationStarted: false,
    simulationCompleted: false,
    simulationError: false,
  };

  startSimulation = () => {
    const model = this.props.store!.newSimulationStore.getAllModels();
    this.setState(
      { simulationStarted: true, simulationError: false },
      async () => {
        try {
          const rawResponse = await fetch('/api/simulations/', {
            method: 'POST',
            headers: {
              Accept: 'application/json',
              'Content-Type': 'application/json',
              Authorization: 'Bearer ' + localStorage.getItem('token'),
            },
            body: JSON.stringify({ parsed: JSON.stringify(model) }),
          });

          if (rawResponse.status === 401) {
            this.props.store?.userStore.logout();
            setTimeout(() => {
              window.location.reload();
            }, 100);
            return;
          }

          const content = await rawResponse.json();

          if (content.success) {
            this.setState({
              simulationCompleted: true,
            });

            this.props.history.push('/details/' + content.simulationId);
          } else {
            this.setState({ simulationError: true });
          }
        } catch (error) {
          // console.log(error);
          this.setState({ simulationError: true });
        }
      }
    );
  };

  render() {
    const classes = classNames({
      'start-button': true,
      'start-button-hide': this.state.simulationStarted,
    });

    return (
      <div className="simulation-padding-container">
        <div className="simulation-flex-container">
          <div className="header-container">
            <h2>Start</h2>
            <div className="buttons-container"></div>
          </div>
        </div>

        {!this.state.simulationError ? (
          <div className="no-content-container" onClick={this.startSimulation}>
            {this.state.simulationStarted ? (
              <Loader type="Circles" color="#264766" height={60} width={60} />
            ) : (
              <div className={classes}>
                <IoIosPlay />
              </div>
            )}

            {!this.state.simulationStarted ? (
              <>
                <p>{TranslationService.start_simulation}</p>
                <p>{TranslationService.start_simulation_description}</p>
              </>
            ) : (
              <>
                <p>{TranslationService.starting}</p>
                <p></p>
              </>
            )}
          </div>
        ) : (
          <div className="no-content-container simulation-error-container">
            <IoIosThumbsDown />
            <p>{TranslationService.something_error}</p>
            <p onClick={this.startSimulation}>{TranslationService.try_again}</p>
          </div>
        )}
      </div>
    );
  }
}

export default withRouter(Start);
