import React from 'react';
import './App.css';
//navigation
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
//mobx
import { Provider } from 'mobx-react';
import Store from './stores/RootStore';
//components
import Navbar from './components/Navbar';
import Footer from './components/Footer';
//pages
import LoginPage from './pages/login/Login';
import RegisterPage from './pages/register/Register';
import SimulationsList from './pages/simulationslist/SimulationsList';
import ChangePassword from './pages/change_password/ChangePassword';
import Simulation from './pages/simulation/SimulationMain';
import SimulationDetails from './pages/simulation-details/SimulationDetails';

let store = new Store();

const PrivateRoute = ({
  component,
  ...rest
}: {
  component: any;
  path: string;
}) => {
  const Component = component;
  return (
    <Route
      {...rest}
      render={(props) =>
        localStorage.getItem('token') !== null &&
        localStorage.getItem('token') !== '' ? (
          <Component {...props} />
        ) : (
          <Redirect to="/login" />
        )
      }
    />
  );
};

function App() {
  return (
    <Provider store={store}>
      <div className="App">
        <Router>
          <Navbar />
          <Switch>
            <PrivateRoute path="/simulations" component={SimulationsList} />
            <PrivateRoute path="/details/:id" component={SimulationDetails} />
            <PrivateRoute path="/change_password" component={ChangePassword} />
            <PrivateRoute path="/simulation" component={Simulation} />
            <Route path="/register" component={RegisterPage} />
            <Route path="/login" component={LoginPage} />
            <PrivateRoute path="/" component={SimulationsList} />
          </Switch>
          <Footer />
        </Router>
      </div>
    </Provider>
  );
}

export default App;
