import React from 'react';
//styles
import './SimulationTemplateModal.css';
import {
  SimulationTemplate,
  SimulationTemplatesList,
} from '../../models/SimulationTemplate.model';
import { IoIosAdd } from 'react-icons/io';

interface IProps {
  emptySimulationPressed: () => void;
  templateSimulationPressed: (simulation: SimulationTemplate) => void;
}

const SimulationTemplateModal = (props: IProps) => {
  return (
    <div className="template-modal-container">
      <div className="template-modal-content">
        <div className="template-modal-content-header">
          <h2>New simulation</h2>
        </div>
        <div className="template-modal-content-inside">
          <div className="template-modal-content-column">
            <div
              className="template-modal-add-empty-button"
              onClick={props.emptySimulationPressed}
            >
              <div className="template-modal-add-empty-button-icon">
                <IoIosAdd />
              </div>
              <label>New empty simulation</label>
            </div>
          </div>
          <div className="template-modal-separator" />
          <div className="template-modal-content-column">
            <label>or use available templates:</label>
            {SimulationTemplatesList.map((item, index) => (
              <div
                key={'template' + index}
                className="template-modal-template-item"
                onClick={() => props.templateSimulationPressed(item)}
              >
                <h5>{item.title}</h5>
                <p>{item.description}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SimulationTemplateModal;
