import React from 'react';
import { IoIosCheckmarkCircle } from 'react-icons/io';
//models
import { Version } from '../models/Version.model';
//utils
import { classNames } from '../utils/ClassNames';

interface IProps {
  active: boolean;
  onPress: (event: any) => void;
  version: Version
}

class VersionListElement extends React.Component<IProps> {
  render() {
    const {version, active, onPress} = this.props;

    const classes = classNames({
      'medium-list-element': true,
      'medium-list-element-active': active,
    });

    return (
      <div className={classes} onClick={onPress}>
        <IoIosCheckmarkCircle />
        <div className="medium-text-container">{version.name}</div>
      </div>
    );
  }
}

export default VersionListElement;