import * as React from 'react';
import './Diagrams.css';
import { Application } from './TopologyDiagram';
import { TrayItemWidget } from './TryItemWidget';
import { CanvasWidget } from '@projectstorm/react-canvas-core';
import { DemoCanvasWidget } from './DemoCanvasWidget';
import { CustomNodeModel } from './node-widget/NodeModel';
import RootStore from '../../stores/RootStore';
import { inject, observer } from 'mobx-react';
import Tooltip from '../tooltip/Tooltip';
import TranslationService from '../../services/TranslationService';

export interface BodyWidgetProps {
  app: Application;
  store?: RootStore;
}

@inject('store')
@observer
class BodyWidget extends React.Component<BodyWidgetProps> {
  render() {
    const {
      listOfAllChannels,
      deviceTypes,
    } = this.props.store!.newSimulationStore;

    return (
      <div className="diagram-body">
        <div className="diagram-content">
          <div className="diagram-tray">
            <div className="tray-header-description">
              <div className="header-inside-container">
                <h2>{TranslationService.topology}</h2>
                <Tooltip
                  type="small"
                  placement="bottom"
                  title={TranslationService.topology}
                  content={TranslationService.topology_tooltip}
                />
              </div>

              <h3>{TranslationService.available_devices}</h3>
              <label>{TranslationService.drag_and_drop}</label>
            </div>

            {deviceTypes.map((v, index) => (
              <TrayItemWidget
                key={'TrayItemWidget-' + index}
                model={{
                  deviceType: v,
                  channels: listOfAllChannels,
                  deviceIndexInList: index,
                }}
              />
            ))}
          </div>
          <div
            className="diagram-layer"
            onDrop={(event) => {
              const data = JSON.parse(
                event.dataTransfer.getData('storm-diagram-node')
              );
              const node = new CustomNodeModel({
                deviceType: data.deviceType,
                channels: data.channels,
              });
              const point = this.props.app
                .getDiagramEngine()
                .getRelativeMousePoint(event);

              node.setPosition(point);
              this.props.app.getDiagramEngine().getModel().addNode(node);

              this.forceUpdate();
            }}
            onDragOver={(event) => {
              event.preventDefault();
            }}
          >
            <DemoCanvasWidget>
              <CanvasWidget engine={this.props.app.getDiagramEngine()} />
            </DemoCanvasWidget>
          </div>
        </div>
      </div>
    );
  }
}

export { BodyWidget };
