import React from 'react';
//styles
import './Select.css';
//components
import Dropdown, { Option } from 'react-dropdown';
import TranslationService from '../../services/TranslationService';

export interface SelectValue {
  label: string;
  value: any;
}
interface IProps {
  items: SelectValue[];
  onChange?: (v: any) => void;
}

interface IState {
  value: SelectValue | undefined;
}
export default class Select extends React.Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      value: props.items ? props.items[0] : undefined,
    };
  }

  onChange = (v: Option) => {
    this.setState(
      {
        value: {
          label: v.value,
          value:
            this.props.items.find((f) => v.value === f.label)?.value || v.value,
        },
      },
      () => {
        this.props.onChange && this.props.onChange(this.state.value?.value);
      }
    );
  };

  getValue = () => {
    if (!this.state.value) {
      return !!this.props.items ? this.props.items[0] : undefined;
    }
    return this.state.value;
  };

  setValue = (value: any) => {
    this.setState({
      value: {
        label: this.props.items.find((f) => value === f.value)?.label || value,
        value: value,
      },
    });
  };

  render() {
    const { value } = this.state;
    const { items } = this.props;

    return (
      <Dropdown
        options={items.map((v) => v.label)}
        onChange={this.onChange}
        value={value?.label}
        placeholder={TranslationService.select_option}
      />
    );
  }
}
