import React from 'react';
import './SimulationDetails.css';
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
import { withRouter } from 'react-router-dom';
import { SimulationListElement as SimulationListElementModel } from '../../models/SimulationListElement.model';
import moment from 'moment';
import { classNames } from '../../utils/ClassNames';
import TranslationService from '../../services/TranslationService';
import { map, trim } from 'lodash';

interface IProps {
  store?: RootStore;
  navigation?: any;
  history: any;
  location: any;
  match: any;
}

interface IState {
  simulation?: SimulationListElementModel;
  loading: boolean;
}

const StatusDictionary: { [key in string]: string } = {
  '1': 'Running',
  '2': 'Passed',
  '3': 'Error',
};

@inject('store')
@observer
class SimulationDetails extends React.Component<IProps, IState> {
  state: IState = {
    simulation: undefined,
    loading: true,
  };

  componentDidMount = () => {
    this.getSimulation();
  };

  getSimulation = async () => {
    try {
      const link = '/api/simulations/' + this.props.match.params.id;
      const rawResponse = await fetch(link, {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + localStorage.getItem('token'),
        },
      });

      if (rawResponse.status === 401) {
        this.props.store?.userStore.logout();
        setTimeout(() => {
          window.location.reload();
        }, 100);
        return;
      }

      const content = await rawResponse.json();

      if (content.success) {
        this.setState({ simulation: content.data, loading: false });
      }
    } catch (error) {
      // console.log(error);
      this.setState({ loading: false });
    }
  };

  renderRaport = () => {
    const simulation = this.state.simulation!;

    if (simulation.status === '2') {
      let data = simulation
        .results!.split(
          '--------------------------------------------------------------------------------'
        )
        .map((w) => {
          return w
            .split('\n')
            .map((v) => v.trim())
            .filter((v) => v.length !== 0);
        })
        .filter((v) => v.length !== 0);

      return (
        <>
          {data?.map((v) => {
            const header = v[1].split('\t');
            const results = v.slice(2, v.length - 2).map((v) => v.split('\t'));
            const rest = v.slice(v.length - 2).join('\n\n');

            return (
              <div className="passed-result-container">
                <div className="passed-header">{header[1]}</div>
                <div className="passed-result">
                  <div className="passed-result-column passed-result-column-bold">
                    {TranslationService.device}
                  </div>
                  <div className="passed-result-column passed-result-column-bold">
                    {TranslationService.time_in_s}
                  </div>
                  <div className="passed-result-column passed-result-column-bold">
                    {TranslationService.status}
                  </div>
                </div>

                {results.map((w) => (
                  <div className="passed-result">
                    <div className="passed-result-column">{w[0]}</div>
                    <div className="passed-result-column">{w[1]}</div>
                    <div
                      className={
                        'passed-result-column ' +
                        (w[2] === 'Finished'
                          ? 'status-text-passed'
                          : 'status-text-error')
                      }
                    >
                      {w[2]}
                    </div>
                  </div>
                ))}
                <div className="results-messages">{rest}</div>
              </div>
            );
          })}
        </>
      );
    } else {
      return (
        <div className="details-error-container">{simulation.results}</div>
      );
    }
  };

  goToSimulationsList = () => {
    this.props.history.push('/simulations');
  };

  render() {
    const { simulation, loading } = this.state;

    const classes = classNames({
      'status-text-error': simulation?.status === '3',
      'status-text-passed': simulation?.status === '2',
    });

    return (
      <div className="main-default-container">
        <div className="header-container">
          <div>
            <h2>
              <p onClick={this.goToSimulationsList} className="back-text">
                {TranslationService.go_back}
              </p>
              {TranslationService.simulation_details}
            </h2>
          </div>
        </div>

        {!loading && (
          <div className="section simulation-details">
            {simulation ? (
              <>
                <div className="details-container">
                  <h5>{TranslationService.date}</h5>
                  <p>
                    {moment(simulation.createdAt).format('YYYY-MM-DD HH:mm:ss')}
                  </p>
                  <h5>{TranslationService.status}</h5>
                  <p className={classes}>
                    {StatusDictionary[simulation.status]}
                  </p>
                </div>
                <h5>{TranslationService.details}</h5>
                {this.renderRaport()}
              </>
            ) : (
              <div className="big-loader-container">
                <p>{TranslationService.simulation_not_found}</p>
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default withRouter(SimulationDetails);
