import React from 'react';
//models
import { DeviceType, Icons } from '../models/DeviceType.model';
//utils
import { classNames } from '../utils/ClassNames';

interface IProps {
  active: boolean;
  onPress: (event: any) => void;
  device: DeviceType;
}

class DeviceTypeListElement extends React.Component<IProps> {
  render() {
    const { device, active, onPress } = this.props;

    const classes = classNames({
      'device-type-list-element': true,
      'device-type-list-element-active': active,
    });

    const Icon = Icons.find((value) => value.name === device.icon)?.icon;

    return (
      <div className={classes} onClick={onPress}>
        {!!Icon && <Icon />}
        <div className="text-container">{device.name}</div>
      </div>
    );
  }
}

export default DeviceTypeListElement;
