import { observable, action } from 'mobx';
import { RootStore } from './RootStore';
import { SimulationListElement } from '../models/SimulationListElement.model';

class SimulationsStore {
  rootStore: RootStore;

  @observable simulations: SimulationListElement[] = [];
  @observable loadingSimulations: boolean = false;
  @observable simulationsCount: number = 0;
  @observable currentSimulationsPage: number = 0;

  constructor(rootStore: any) {
    this.rootStore = rootStore;
  }

  @action
  getSimulationsList = async () => {
    this.loadingSimulations = true;

    try {
      const link = '/api/simulations?page=' + this.currentSimulationsPage;
      const rawResponse = await fetch(link, {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + localStorage.getItem('token'),
        },
      });

      if (rawResponse.status === 401) {
        this.rootStore.userStore.logout();
        setTimeout(() => {
          window.location.reload();
        }, 100);

        return;
      }

      console.log(rawResponse);

      const content = await rawResponse.json();

      if (content.success) {
        this.loadingSimulations = false;
        this.simulations = content.data;
        this.simulationsCount = content.count;
      }
    } catch (error) {
      console.log(error);
      this.loadingSimulations = false;
    }
  };

  @action
  changeSimulationPage = (value: number) => {
    this.currentSimulationsPage = value;
  };
}

export default SimulationsStore;
