import React from 'react';
import './SimulationsList.css';
import ReactPaginate from 'react-paginate';
//mobx
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import RootStore from '../../stores/RootStore';
//navigation
import { withRouter, Link } from 'react-router-dom';
//components
import SimulationListElement from '../../components/simulation-list-element/SimulationListElement';
import { IoIosAdd, IoIosRefresh } from 'react-icons/io';
//@ts-ignore
import Loader from 'react-loader-spinner';
//services
import TranslationService from '../../services/TranslationService';

interface IProps {
  store?: RootStore;
  navigation?: any;
  history: any;
  location: any;
  match: any;
}

@inject('store')
@observer
class SimulationsListPage extends React.Component<IProps> {
  state = {
    redirectToReferrer: false,
  };

  componentDidMount = () => {
    this.props.store?.simulationsStore.getSimulationsList();
  };

  onChangePage = ({ selected }: { selected: number }) => {
    this.props.store?.simulationsStore.changeSimulationPage(selected);
    this.props.store?.simulationsStore.getSimulationsList();
  };

  render() {
    const {
      simulations,
      simulationsCount,
      loadingSimulations,
      getSimulationsList,
    } = this.props.store?.simulationsStore!;

    return (
      <div className="main-default-container">
        <div className="header-container">
          <h2>{TranslationService.my_simulations}</h2>
          <div className="buttons-container">
            <div
              onClick={getSimulationsList}
              className="round-button round-button-secondary refresh-button"
            >
              <IoIosRefresh />
            </div>
            <div className="round-button round-button-primary">
              <IoIosAdd />
              <Link to="/simulation">
                <p>{TranslationService.create_simulation}</p>
              </Link>
            </div>
          </div>
        </div>

        <div className="section">
          {!loadingSimulations ? (
            <>
              {simulations.map((simulation, index) => (
                <SimulationListElement
                  key={'simulation' + index + simulation.createdAt}
                  simulation={toJS(simulation)}
                />
              ))}
              {simulationsCount === 0 && (
                <div className="big-loader-container">
                  <p>No simulations.</p>
                </div>
              )}
            </>
          ) : (
            <div className="big-loader-container">
              <Loader type="Circles" color="#264766" height={40} width={40} />
            </div>
          )}

          <ReactPaginate
            pageCount={Math.ceil(simulationsCount / 10)}
            pageRangeDisplayed={4}
            marginPagesDisplayed={1}
            containerClassName="pagination-container"
            pageLinkClassName="pagination-page"
            previousLinkClassName="pagination-navigation-button pagination-left-button"
            nextLinkClassName="pagination-navigation-button pagination-right-button"
            previousLabel="Previous"
            nextLabel="Next"
            activeLinkClassName="pagination-page-active"
            onPageChange={this.onChangePage}
          />
        </div>
      </div>
    );
  }
}

export default withRouter(SimulationsListPage);
