import React from 'react';
import moment from 'moment';
//models
import { SimulationListElement as SimulationModel } from '../../models/SimulationListElement.model';
//styles
import './SimulationListElement.css';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';
//components
//@ts-ignore
import Loader from 'react-loader-spinner';
//utils
import { classNames } from '../../utils/ClassNames';
import { Link } from 'react-router-dom';
import TranslationService from '../../services/TranslationService';

interface IProps {
  simulation: SimulationModel;
}

class SimulationListElement extends React.Component<IProps> {
  state = {};

  renderStatusInfo = (status: string, id: number) => {
    switch (status) {
      case '2':
        return (
          <>
            <span className="status-column status-column-passed">Passed</span>
            <Link
              className="details-column details-column-link"
              to={'/details/' + id}
            >
              {TranslationService.details}
            </Link>
          </>
        );
      case '3':
        return (
          <>
            <span className="status-column status-column-error">Error</span>
            <Link
              className="details-column details-column-link"
              to={'/details/' + id}
            >
              {TranslationService.details}
            </Link>
          </>
        );
      case '0':
        return (
          <>
            <span className="status-column"> {TranslationService.running}</span>
            <div className="details-column">
              <Loader type="Circles" color="#264766" height={20} width={20} />
            </div>
          </>
        );
    }
  };

  render() {
    const { simulation } = this.props;

    const classes = classNames({
      'status-indicator': true,
      'status-indicator-pending': simulation.status === '4',
      'status-indicator-passed': simulation.status === '2',
      'status-indicator-error': simulation.status === '3',
      'status-indicator-running': simulation.status === '0',
    });

    return (
      <div className="simulation-container">
        <div className={classes} />
        <div className="simulation-inside-container">
          <p>{moment(simulation.createdAt).format('DD.MM.YYYY HH:mm')}</p>
          {this.renderStatusInfo(simulation.status, simulation.id)}
        </div>
      </div>
    );
  }
}

export default SimulationListElement;
