import * as React from 'react';
import { DeviceType, Icons } from '../../models/DeviceType.model';
import { Channel } from '../../models/Medium.model';

export interface TrayItemWidgetProps {
	model: {deviceType: DeviceType, channels: Channel[], deviceIndexInList: number};
}

export class TrayItemWidget extends React.Component<TrayItemWidgetProps> {
	render() {

		const Icon = Icons.find(value => value.name === this.props.model.deviceType.icon)?.icon

		return (
			<div 
				className="diagram-tray-item tray-item"
				draggable={true}
				onDragStart={(event) => {
					event.dataTransfer.setData('storm-diagram-node', JSON.stringify(this.props.model));
				}}>
					{Icon && <Icon />}
					<label>{this.props.model.deviceType.name}</label>
			</div>
		);
	}
}