import React from 'react';
//styles
import './IconPicker.css';
//mobx
import RootStore from '../../stores/RootStore';
//utils
import { classNames } from '../../utils/ClassNames';
// models
import { Icons } from '../../models/DeviceType.model';

interface IProps {
  store?: RootStore;
}

class IconPicker extends React.Component<IProps> {

  state = {
    value: 0
  };

  setValue = (name: string) => {
    this.setState({value: Icons.findIndex(value => value.name === name)});
  }

  getValue = () => {
    return Icons[this.state.value].name;
  }

  render() {
    return (
      <div className="icon-picker-container" >
          {Icons.map((Element, index: number) => {
            const classes = classNames({
              'icon-picker-item': true,
              'icon-picker-item-active': index === this.state.value,
            });

            return (
              <div
                key={"icon" + index}
                className={classes}
                onClick={() => this.setValue(Element.name)}
              >
                <Element.icon color={index === this.state.value ? '#264766' : 'gray'}/>
              </div>
            )       
          })
        }
      </div>
    );
  }
}

export default IconPicker;