import * as React from 'react';
import { CustomNodeModel } from './NodeModel';
import { NodeWidget } from './NodeWidget';
import { AbstractReactFactory } from '@projectstorm/react-canvas-core';
import { DiagramEngine } from '@projectstorm/react-diagrams-core';

export class NodeFactory extends AbstractReactFactory<CustomNodeModel, DiagramEngine> {
	constructor() {
		super('ts-custom-node');
	}

	generateModel(initialConfig: any) {
		return new CustomNodeModel(initialConfig);
	}

	generateReactWidget(event: any): JSX.Element {
		return <NodeWidget engine={this.engine as DiagramEngine} node={event.model} />;
	}
}