import { observable, action } from 'mobx';
import { RootStore } from './RootStore';

class UserStore {
  rootStore: RootStore;

  @observable
  token = localStorage.getItem('token') || '';

  @observable
  registeredPrompt = false;

  @observable
  fetchingRegistration = false;

  @observable
  fetchingLogging = false;

  @observable
  fetchingPasswordChanging = false;

  @observable
  loginError = false;

  @observable
  registrationError = false;

  @observable
  changePasswordError = false;

  @observable
  invalidOldUserPasswordError = false;

  @observable
  userId: number = 0;

  constructor(rootStore: any) {
    this.rootStore = rootStore;
  }

  @action
  hideRegisteredPrompt = () => {
    this.registeredPrompt = false;
  };

  @action
  login = async (data: Object, callback?: () => void) => {
    this.loginError = false;
    this.fetchingLogging = true;
    this.registeredPrompt = false;

    try {
      const rawResponse = await fetch('/api/login', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
      const content = await rawResponse.json();

      if (content.success) {
        this.fetchingLogging = false;
        this.loginError = false;
        localStorage.setItem('token', content.token);
        this.token = content.token;
        callback && callback();
      } else {
        this.loginError = true;
        this.fetchingLogging = false;
      }
    } catch (error) {
      // console.log(error);
      this.loginError = true;
      this.fetchingLogging = false;
    }
  };

  @action
  register = async (
    data: Object,
    callback?: () => void,
    onError?: (error: any) => void
  ) => {
    this.registrationError = false;
    this.fetchingRegistration = true;

    try {
      const rawResponse = await fetch('/api/signup', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
      const content = await rawResponse.json();

      if (content.success) {
        this.fetchingRegistration = false;
        this.registrationError = false;
        this.registeredPrompt = true;
        callback && callback();
      } else {
        throw content;
      }
    } catch (error) {
      onError && onError(error);
      this.registrationError = true;
      this.fetchingRegistration = false;
    }
  };

  @action
  changePassword = async (
    data: Object,
    callback?: () => void,
    onInvalidOldPassword?: () => void
  ) => {
    this.changePasswordError = false;
    this.fetchingPasswordChanging = true;

    try {
      const rawResponse = await fetch('/api/change_password', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + localStorage.getItem('token'),
        },
        body: JSON.stringify(data),
      });

      if (rawResponse.status === 401) {
        this.rootStore.userStore.logout();
        setTimeout(() => {
          window.location.reload();
        }, 100);
        return;
      }

      const content = await rawResponse.json();

      if (content.success) {
        this.fetchingPasswordChanging = false;
        this.changePasswordError = false;
        callback && callback();
      } else {
        if (content.message === 'Incorrect user password.') {
          onInvalidOldPassword && onInvalidOldPassword();
        } else {
          this.changePasswordError = true;
        }
        this.fetchingPasswordChanging = false;
      }
    } catch (error) {
      // console.log(error);
      this.changePasswordError = true;
      this.fetchingPasswordChanging = false;
    }
  };

  @action
  logout = (callback?: () => void) => {
    callback && callback();

    setTimeout(() => {
      localStorage.clear();
      this.token = '';
    }, 100);
  };
}

export default UserStore;
