import * as React from 'react';
import { DiagramEngine, PortWidget } from '@projectstorm/react-diagrams-core';
import { CustomNodeModel } from './NodeModel';
import { PortModel, PortModelGenerics } from '@projectstorm/react-diagrams';
import { Icons } from '../../../models/DeviceType.model';
import TranslationService from '../../../services/TranslationService';

export interface TSCustomNodeWidgetProps {
  node: CustomNodeModel;
  engine: DiagramEngine;
}

export interface TSCustomNodeWidgetState {}

export class NodeWidget extends React.Component<
  TSCustomNodeWidgetProps,
  TSCustomNodeWidgetState
> {
  constructor(props: TSCustomNodeWidgetProps) {
    super(props);
    this.state = {};
  }

  getPorts = () => {
    const Ports = this.props.node.getPorts();
    let InPorts: PortModel<PortModelGenerics>[] = [];
    let OutPorts: PortModel<PortModelGenerics>[] = [];

    Object.keys(Ports).forEach((v) => {
      const port = Ports[v];

      if (v.includes('In')) {
        InPorts.push(port);
      } else {
        OutPorts.push(port);
      }
    });

    return { InPorts, OutPorts };
  };

  removeNode = () => {
    this.props.node.remove();
  };

  render() {
    const { deviceType, deviceId } = this.props.node;
    const { InPorts, OutPorts } = this.getPorts();
    const channelsNames = this.props.node.channels
      ? this.props.node.channels.map((v) => v.name)
      : [];
    const Icon = Icons.find((value) => value.name === deviceType?.icon)?.icon;

    return (
      <div className="custom-node">
        <div className="node-header">
          <div className="node-header-inside">
            {Icon && <Icon />}
            <div className="node-header-text">
              <label>
                {TranslationService.device_instance} #{deviceId}
              </label>
              <label>{deviceType?.name}</label>
            </div>
          </div>
        </div>

        <div className="node-ports-container">
          <div className="ports in-ports">
            {InPorts.map((v, index) => (
              <PortWidget
                key={'in-port-' + index}
                engine={this.props.engine}
                port={v}
              >
                <div className="node-port node-port-in">
                  <label>{TranslationService.in}</label>
                </div>
              </PortWidget>
            ))}
          </div>

          <div className="ports ports-names">
            {channelsNames.map((v, index) => (
              <label key={'name-port-' + index}>{v}</label>
            ))}
          </div>

          <div className="ports out-ports">
            {OutPorts.map((v, index) => (
              <PortWidget
                key={'out-port-' + index}
                engine={this.props.engine}
                port={v}
              >
                <div className="node-port node-port-out">
                  <label>{TranslationService.out}</label>
                </div>
              </PortWidget>
            ))}
          </div>
        </div>
      </div>
    );
  }
}
