import React from 'react';
//styles
import './Simulation.css';
//mobx
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
//components
import { BodyWidget } from '../../components/topology-diagram/BodyWidget';
import { Application } from '../../components/topology-diagram/TopologyDiagram';
import { SimulationTemplate } from '../../models/SimulationTemplate.model';

interface IProps {
  store?: RootStore;
  defaultModel?: SimulationTemplate;
}

let app = new Application();

@inject('store')
@observer
class Topology extends React.Component<IProps> {
  componentDidMount = () => {
    this.props.defaultModel
      ? this.setNewModel(this.props.defaultModel)
      : app.newModel();
  };

  serialize = () => {
    const { changeTopologyElements } = this.props.store!.newSimulationStore;
    const serialized = app.prepareDiagramToAnalyse();
    changeTopologyElements(serialized);
  };

  setNewModel = (model: SimulationTemplate) => {
    app.deserializeDiagram(model.topologyDiagrams);
  };

  resetApp = () => {};

  render() {
    return (
      <div className="simulation-padding-container">
        <BodyWidget app={app} />
      </div>
    );
  }
}

export default Topology;
