import React from 'react';
//styles
import './Process.css';
import Input from '../Input';
import { IoIosTrash } from 'react-icons/io';
import Select, { SelectValue } from '../select/Select';
import NumberInput from '../number-input/NumberInput';
import { Process as ProcessModel } from '../../models/Version.model';
import { observer, inject } from 'mobx-react';
import RootStore from '../../stores/RootStore';
import _ from 'lodash';
import Checkbox from '../checkbox/Checkbox';
import TranslationService from '../../services/TranslationService';
import Tooltip from '../tooltip/Tooltip';

interface IProps {
  process: ProcessModel;
  removeProcess?: () => void;
  store?: RootStore;
}

interface IState {
  channelsList: SelectValue[];
  sendDevicesList: SelectValue[];
  deviceId: number | undefined;
  isOutProcess: boolean;
  isInProcess: boolean;
}

@inject('store')
@observer
class Process extends React.Component<IProps, IState> {
  nameInputRef = React.createRef<Input>();
  deviceInstanceSelectRef = React.createRef<Select>();
  executionTimeInputRef = React.createRef<NumberInput>();
  processTypeSelectRef = React.createRef<Select>();
  inChannelSelectRef = React.createRef<Select>();
  outChannelSelectRef = React.createRef<Select>();
  senderDeviceSelectRef = React.createRef<Select>();
  outMessageSizeRef = React.createRef<NumberInput>();

  infinityCheckboxRef = React.createRef<Checkbox>();
  isOutProcessCheckboxRef = React.createRef<Checkbox>();
  isInProcessCheckboxRef = React.createRef<Checkbox>();

  constructor(props: IProps) {
    super(props);

    this.state = {
      isOutProcess: false,
      isInProcess: false,
      deviceId: undefined,
      channelsList: [],
      sendDevicesList: [],
    };
  }

  validate = (index?: number) => {
    const {
      checkProcessNameUniqueness,
    } = this.props.store?.newSimulationStore!;
    const validation = [this.nameInputRef.current?.validate()];

    if (index) {
      if (
        !checkProcessNameUniqueness(
          this.nameInputRef.current?.getValue()!,
          index
        )
      ) {
        this.nameInputRef.current?.setValidity(true, "The name isn't unique.");
        return false;
      }
    }

    return validation.findIndex((v) => !v) === -1;
  };

  getValue = () => {
    const process: ProcessModel = {
      name: this.nameInputRef.current?.getValue() || '',
      time: this.executionTimeInputRef.current?.getValue() || 1.0,
      deviceId: this.deviceInstanceSelectRef.current?.getValue()?.value,
      deviceToSendId: this.senderDeviceSelectRef.current?.getValue()?.value,
      inChannel: this.inChannelSelectRef.current?.getValue()?.value,
      outChannel: this.outChannelSelectRef.current?.getValue()?.value,
      isInfinity: this.infinityCheckboxRef.current?.getValue(),
      outMessageSize: this.outMessageSizeRef.current?.getValue() || 1,
    };

    return process;
  };

  setValue = (process: ProcessModel) => {
    this.nameInputRef.current?.setValue(process.name);
    this.executionTimeInputRef.current?.setValue(process.time);
    this.deviceInstanceSelectRef.current?.setValue(process.deviceId);
    this.infinityCheckboxRef.current?.setValue(process.isInfinity || false);

    this.setState(
      {
        isInProcess: !!process.inChannel,
        isOutProcess: !!process.deviceToSendId && !!process.outChannel,
      },
      () => {
        this.isInProcessCheckboxRef.current?.setValue(this.state.isInProcess);
        this.isOutProcessCheckboxRef.current?.setValue(this.state.isOutProcess);
        this.inChannelSelectRef.current?.setValue(process.inChannel);
        this.senderDeviceSelectRef.current?.setValue(process.deviceToSendId);
        this.outChannelSelectRef.current?.setValue(process.outChannel);
        this.outMessageSizeRef.current?.setValue(process.outMessageSize!);
      }
    );
  };

  onChangeInChannelVisibility = (value: boolean) => {
    this.setState({ isInProcess: value });
  };

  onChangeOutChannelVisibility = (value: boolean) => {
    this.setState({ isOutProcess: value });
  };

  getChannelsList = () => {
    const { mediums } = this.props.store!.newSimulationStore;

    let channels: string[] = [];
    mediums.forEach((v) => {
      v.channels.forEach((w) => channels.push(w.name));
    });

    return channels.map((v) => ({
      label: v,
      value: v,
    }));
  };

  onChangeChannel = (value: string) => {
    const { topologyElements } = this.props.store!.newSimulationStore;

    const sendDevices = topologyElements
      .find((v) => v.deviceId === this.state.deviceId)
      ?.connectedOutDevicesIds.filter((v) => v.channelName === value)
      .map((v) => v.deviceId);

    this.setState({
      sendDevicesList: _.uniq(sendDevices).map((v) => ({
        label: 'Device instance #' + v,
        value: v.toString(),
      })),
    });
  };

  render() {
    const { process, removeProcess, store } = this.props;
    const { isInProcess, isOutProcess } = this.state;
    const { topologyElements } = store!.newSimulationStore;

    const channelsList = this.getChannelsList();

    const devicesInstances = topologyElements.map((v) => ({
      label: 'Device instance #' + v.deviceId,
      value: v.deviceId,
    }));

    return (
      <div className="section">
        <div className="label-inside-container">
          <label>{TranslationService.process_name}</label>
          <Tooltip
            type="small"
            placement="bottom"
            title={TranslationService.process_name}
            content={TranslationService.process_name_tooltip}
          />
        </div>
        <div className="channel-name-container">
          <div className="delete-input-conatiner">
            <Input
              ref={this.nameInputRef}
              initialValue={process.name}
              white
              type="elementName"
            />
          </div>
          <div
            onClick={removeProcess}
            className="round-button round-button-secondary round-button-remove"
          >
            <IoIosTrash />
          </div>
        </div>

        <div className="medium-constants-row process-info-row">
          <div className="medium-constants-column">
            <div className="label-inside-container">
              <label>{TranslationService.device_instance}</label>
              <Tooltip
                type="small"
                placement="bottom"
                title={TranslationService.device_instance}
                content={TranslationService.process_device_instance_tooltip}
              />
            </div>
            <Select
              ref={this.deviceInstanceSelectRef}
              items={devicesInstances}
            />
          </div>
          <div className="medium-constants-column">
            <div className="label-inside-container">
              <label>{TranslationService.execution_time}</label>
              <Tooltip
                type="small"
                placement="bottom"
                title={TranslationService.execution_time}
                content={TranslationService.process_execution_time_tooltip}
              />
            </div>
            <NumberInput ref={this.executionTimeInputRef} />
          </div>
        </div>

        <div className="label-inside-container">
          <Checkbox
            ref={this.infinityCheckboxRef}
            placeholder={TranslationService.infinity_process}
          />
          <Tooltip
            type="small"
            placement="bottom"
            title={TranslationService.is_infinity}
            content={TranslationService.process_infinity_tooltip}
          />
        </div>

        <div className="label-inside-container">
          <Checkbox
            ref={this.isInProcessCheckboxRef}
            placeholder={TranslationService.process_with_recieve}
            onChange={this.onChangeInChannelVisibility}
          />
          <Tooltip
            type="small"
            placement="bottom"
            title={TranslationService.process_with_recieve}
            content={TranslationService.process_recieve_tooltip}
          />
        </div>

        {isInProcess && (
          <>
            <label>{TranslationService.recieve_from_channel}</label>
            <Select
              items={channelsList}
              ref={this.inChannelSelectRef}
              onChange={this.onChangeChannel}
            />
          </>
        )}

        <div className="label-inside-container">
          <Checkbox
            ref={this.isOutProcessCheckboxRef}
            placeholder={TranslationService.process_with_send}
            onChange={this.onChangeOutChannelVisibility}
          />
          <Tooltip
            type="small"
            placement="bottom"
            title={TranslationService.process_with_send}
            content={TranslationService.process_send_tooltip}
          />
        </div>

        {isOutProcess && (
          <>
            <label>{TranslationService.send_channel}</label>
            <Select
              items={channelsList}
              ref={this.outChannelSelectRef}
              onChange={this.onChangeChannel}
            />
            <label>{TranslationService.device_to_sent}</label>
            <Select items={devicesInstances} ref={this.senderDeviceSelectRef} />
            <label>{TranslationService.size_to_sent}</label>
            <NumberInput ref={this.outMessageSizeRef} />
          </>
        )}
        <div className="separator" />
      </div>
    );
  }
}

export default Process;
