import React from 'react';
import { IoIosFastforward } from 'react-icons/io';
//models
import { Medium as MediumModel } from '../models/Medium.model';
//utils
import { classNames } from '../utils/ClassNames';

interface IProps {
  active: boolean;
  onPress: (event: any) => void;
  medium: MediumModel
}

class MediumListElement extends React.Component<IProps> {
  render() {
    const {medium, active, onPress} = this.props;

    const classes = classNames({
      'medium-list-element': true,
      'medium-list-element-active': active,
    });

    return (
      <div className={classes} onClick={onPress}>
        <IoIosFastforward />
        <div className="medium-text-container">{medium.name}</div>
      </div>
    );
  }
}

export default MediumListElement;