import React from 'react';
import { IoIosAdd, IoIosSave, IoIosTrash } from 'react-icons/io';
//styles
import './Simulation.css';
//mobx
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
//components
import Input from '../../components/Input';
import Tooltip from '../../components/tooltip/Tooltip';
import DeviceTypeListElement from '../../components/DeviceTypeListElement';
import IconPicker from '../../components/icon-picker/IconPicker';
import Select, { SelectValue } from '../../components/select/Select';
//models
import {
  DeviceType as DeviceTypeModel,
  CPUs,
  OSes,
  Libraries,
} from '../../models/DeviceType.model';
//services
import TranslationService from '../../services/TranslationService';

interface IProps {
  store?: RootStore;
}

const ScheduleTypes: SelectValue[] = [
  { label: 'Round robin', value: 'rr' },
  { label: 'FIFO', value: 'fifo' },
];

@inject('store')
@observer
class DevicesTypes extends React.Component<IProps> {
  iconPickerRef = React.createRef<IconPicker>();
  nameInputRef = React.createRef<Input>();
  cpuSelectRef = React.createRef<Select>();
  osSelectRef = React.createRef<Select>();
  libsSelectRef = React.createRef<Select>();
  schedulingAlgorithmSelectRef = React.createRef<Select>();

  setDataToForm = () => {
    const {
      deviceTypes,
      activeDeviceType,
    } = this.props.store!.newSimulationStore;

    if (activeDeviceType !== undefined) {
      const device = deviceTypes[activeDeviceType];

      const iconPickerRef = this.iconPickerRef.current;
      const nameInputRef = this.nameInputRef.current;
      const cpuInputRef = this.cpuSelectRef.current;
      const osInputRef = this.osSelectRef.current;
      const librariesInputRef = this.libsSelectRef.current;
      const schedulingAlgorithmSelectRef = this.schedulingAlgorithmSelectRef
        .current;

      if (
        iconPickerRef &&
        nameInputRef &&
        cpuInputRef &&
        osInputRef &&
        librariesInputRef &&
        schedulingAlgorithmSelectRef
      ) {
        iconPickerRef.setValue(device.icon);
        nameInputRef.setValue(device.name);
        cpuInputRef.setValue(device.cpu);
        osInputRef.setValue(device.os);
        librariesInputRef.setValue(device.libraries);
        schedulingAlgorithmSelectRef.setValue(device.schedulingAlgorithm);
      }
    }
  };

  saveData = () => {
    const {
      editDeviceType,
      activeDeviceType,
      changeActiveDeviceType,
      checkDeviceTypeNameUniqueness,
    } = this.props.store!.newSimulationStore;

    if (activeDeviceType !== undefined) {
      const iconPickerRef = this.iconPickerRef.current;
      const nameInputRef = this.nameInputRef.current;
      const cpuInputRef = this.cpuSelectRef.current;
      const osInputRef = this.osSelectRef.current;
      const librariesInputRef = this.libsSelectRef.current;
      const schedulingAlgorithmSelectRef = this.schedulingAlgorithmSelectRef
        .current;

      if (
        iconPickerRef &&
        nameInputRef &&
        cpuInputRef &&
        osInputRef &&
        librariesInputRef &&
        schedulingAlgorithmSelectRef
      ) {
        const nameValidity = nameInputRef.validate();

        if (nameValidity) {
          const updatedDevice: DeviceTypeModel = {
            icon: iconPickerRef.getValue(),
            name: nameInputRef.getValue(),
            cpu: cpuInputRef.getValue()?.label!,
            os: osInputRef.getValue()?.label!,
            libraries: librariesInputRef.getValue()?.label!,
            schedulingAlgorithm: schedulingAlgorithmSelectRef.getValue()?.value,
          };

          if (!checkDeviceTypeNameUniqueness(updatedDevice.name)) {
            nameInputRef.setValidity(true, "The name isn't unique.");
            return false;
          }

          editDeviceType(updatedDevice, activeDeviceType);
          changeActiveDeviceType(undefined);

          return true;
        } else {
          return false;
        }
      }
    }
    return true;
  };

  changeDevice = (id: number) => {
    const { changeActiveDeviceType } = this.props.store!.newSimulationStore;

    changeActiveDeviceType(id);
    setTimeout(() => {
      this.setDataToForm();
    }, 100);
  };

  addDevice = () => {
    const { addDeviceType } = this.props.store!.newSimulationStore;

    addDeviceType();
    setTimeout(() => {
      this.setDataToForm();
    }, 100);
  };

  render() {
    const {
      deviceTypes,
      activeDeviceType,
      removeDeviceType,
    } = this.props.store!.newSimulationStore;

    return (
      <div className="simulation-padding-container">
        <div className="simulation-flex-container">
          <div className="header-container">
            <div className="header-inside-container">
              <h2>{TranslationService.device_types}</h2>
              <Tooltip
                type="small"
                placement="bottom"
                title={TranslationService.device_types}
                content={TranslationService.device_types_tooltip}
              />
            </div>
            <div className="buttons-container">
              <div
                onClick={this.addDevice}
                className="round-button round-button-primary"
              >
                <IoIosAdd />
                <p>{TranslationService.add_device_types}</p>
              </div>
            </div>
          </div>

          <div className="section ">
            <div className="device-type-list">
              {deviceTypes.map((element: DeviceTypeModel, index: number) => (
                <DeviceTypeListElement
                  key={'' + index}
                  active={activeDeviceType === index}
                  onPress={() => this.changeDevice(index)}
                  device={element}
                />
              ))}
            </div>
          </div>
        </div>

        <div
          className={
            'simulation-flex-container' +
            (activeDeviceType === undefined
              ? ' simulation-flex-container-removed'
              : '')
          }
        >
          <>
            <div className="header-container">
              <h2>{TranslationService.device_details}</h2>
              <div className="buttons-container">
                <div
                  onClick={() => removeDeviceType(activeDeviceType)}
                  className="round-button round-button-secondary round-button-remove"
                >
                  <IoIosTrash />
                </div>

                <div
                  onClick={this.saveData}
                  className="round-button round-button-primary"
                >
                  <IoIosSave />
                  <p>{TranslationService.save}</p>
                </div>
              </div>
            </div>

            <div className="section">
              <h3>{TranslationService.primary}</h3>
              <div className="label-inside-container">
                <label>{TranslationService.device_icon}</label>
                <Tooltip
                  type="small"
                  placement="bottom"
                  title={TranslationService.device_icon}
                  content={TranslationService.device_icon_tooltip}
                />
              </div>

              <IconPicker ref={this.iconPickerRef} />

              <div className="label-inside-container">
                <label>{TranslationService.device_name}</label>
                <Tooltip
                  type="small"
                  placement="bottom"
                  title={TranslationService.device_name}
                  content={TranslationService.device_name_tooltip}
                />
              </div>
              <Input ref={this.nameInputRef} white type="elementName" />

              <div className="label-inside-container">
                <label>{TranslationService.scheduling_algorithm}</label>
                <Tooltip
                  type="small"
                  placement="bottom"
                  title={TranslationService.scheduling_algorithm}
                  content={TranslationService.device_scheduling_tooltip}
                />
              </div>
              <Select
                ref={this.schedulingAlgorithmSelectRef}
                items={ScheduleTypes}
              />

              <div className="label-inside-container label-inside-container-with-bottom">
                <h3>{TranslationService.informational}</h3>
                <Tooltip
                  type="small"
                  placement="bottom"
                  title={TranslationService.informational}
                  content={TranslationService.device_informal_tooltip}
                />
              </div>
              <label>CPU</label>
              <Select ref={this.cpuSelectRef} items={CPUs} />

              <label>OS</label>
              <Select ref={this.osSelectRef} items={OSes} />

              <label>Libraries</label>

              <Select ref={this.libsSelectRef} items={Libraries} />
            </div>
          </>
        </div>

        {deviceTypes.length === 0 && (
          <div className="no-content-container">
            <p>{TranslationService.no_device_added_header}</p>
            <p>{TranslationService.no_device_added_description}</p>
          </div>
        )}
      </div>
    );
  }
}

export default DevicesTypes;
