import { observable, action, computed, toJS } from 'mobx';
import { RootStore } from './RootStore';
import {
  DeviceType as DeviceTypeModel,
  CPUs,
  OSes,
  Libraries,
} from '../models/DeviceType.model';
import {
  Medium as MediumModel,
  Channel as ChannelModel,
} from '../models/Medium.model';
import { Version, Process } from '../models/Version.model';
import { TopologyElement } from '../models/TopologyElement.model';

class SimulationsStore {
  rootStore: RootStore;

  @observable
  activeDeviceType?: number;

  @observable
  activeMedium?: number;

  @observable
  activeVersion?: number;

  @observable
  deviceTypes: DeviceTypeModel[];

  @observable
  mediums: MediumModel[];

  @observable
  versions: Version[];

  @observable
  topologyElements: TopologyElement[];

  constructor(rootStore: any) {
    this.rootStore = rootStore;
    this.deviceTypes = [];
    this.mediums = [];
    this.versions = [];
    this.topologyElements = [];
  }

  @action setDeviceTypes = (deviceTypes: DeviceTypeModel[]) => {
    this.deviceTypes = deviceTypes;
  };

  @action setMediums = (mediums: MediumModel[]) => {
    this.mediums = mediums;
  };

  @action setVersions = (versions: Version[]) => {
    this.versions = versions;
  };

  @action setTopologyElements = (topologyElements: TopologyElement[]) => {
    this.topologyElements = topologyElements;
  };

  @action
  resetDeviceTypes = () => {
    this.deviceTypes = [];
    this.activeDeviceType = undefined;
  };

  @action
  resetMediums = () => {
    this.mediums = [];
    this.activeMedium = undefined;
  };

  @action
  resetTopology = () => {
    this.topologyElements = [];
  };

  @action
  resetVersions = () => {
    this.versions = [];
    this.activeVersion = undefined;
  };

  @action
  addDeviceType = () => {
    this.deviceTypes.push({
      name: 'DeviceType' + (this.deviceTypes.length + 1),
      icon: 'IoIosDesktop',
      cpu: CPUs[0].label,
      os: OSes[0].label,
      libraries: Libraries[0].label,
      schedulingAlgorithm: 'fifo',
    });
    this.activeDeviceType = this.deviceTypes.length - 1;
  };

  @action
  editDeviceType = (device: DeviceTypeModel, id: number) => {
    this.deviceTypes[id] = device;
    this.activeDeviceType = undefined;
  };

  @action
  removeDeviceType = (id: number | undefined) => {
    if (id !== undefined) {
      this.deviceTypes.splice(id, 1);
      this.activeDeviceType = undefined;
    }
  };

  @action
  changeActiveDeviceType = (id: number | undefined) => {
    this.activeDeviceType = this.activeDeviceType === id ? undefined : id;
  };

  @action
  addMedium = () => {
    this.mediums.push({
      name: 'Medium' + (this.mediums.length + 1),
      defaultQuality: 1,
      defaultListeningCurrent: 1,
      defaultSendingCurrent: 1,
      defaultReceivingCurrent: 1,
      channels: [],
    });
    this.activeMedium = this.mediums.length - 1;
  };

  @action
  editMedium = (medium: MediumModel, id: number) => {
    this.mediums[id] = medium;
    this.activeMedium = undefined;
  };

  @action
  removeMedium = (id: number) => {
    this.mediums.splice(id, 1);
    this.activeMedium = undefined;
  };

  @action
  addChannelToMedium = (mediumId: number) => {
    this.mediums[mediumId].channels.push({
      name: 'Channel' + (this.mediums[mediumId].channels.length + 1),
      buffer: 1024,
    });
  };

  @action
  editChannelFromMedium = (
    mediumId: number,
    channel: ChannelModel,
    id: number
  ) => {
    this.mediums[mediumId].channels[id] = channel;
  };

  @computed
  get listOfAllChannels() {
    let channels: ChannelModel[] = [];

    this.mediums.forEach((medium) => {
      medium.channels.forEach((channel) => {
        channels.push(channel);
      });
    });

    return channels;
  }

  @action
  removeChannelFromMedium = (mediumId: number, id: number) => {
    this.mediums[mediumId].channels.splice(id, 1);

    let tmp = [...this.mediums[mediumId].channels];
    this.mediums[mediumId].channels = [];
    setTimeout(() => {
      this.mediums[mediumId].channels = tmp;
    }, 50);
  };

  @action
  changeActiveMedium = (id: number | undefined) => {
    this.activeMedium = this.activeMedium === id ? undefined : id;
  };

  @action
  addVersion = () => {
    this.versions.push({
      name: 'Version' + (this.versions.length + 1),
      processes: [],
    });

    this.activeVersion = this.versions.length - 1;
  };

  @action
  editVersion = (version: Version, id: number) => {
    this.versions[id] = version;
    this.activeVersion = undefined;
  };

  @action
  removeVersion = (id: number) => {
    this.versions.splice(id, 1);
    this.activeVersion = undefined;
  };

  @action
  addProcessToVersion = (versionId: number) => {
    this.versions[versionId].processes.push({
      name: 'Process' + (this.versions[versionId].processes.length + 1),
      time: 100,
    });
  };

  @action
  editProcessInVersion = (versionId: number, process: Process, id: number) => {
    this.versions[versionId].processes[id] = process;
  };

  @action
  removeProcessFromVersion = (versionId: number, id: number) => {
    this.versions[versionId].processes.splice(id, 1);

    let tmp = [...this.versions[versionId].processes];
    this.versions[versionId].processes = [];
    setTimeout(() => {
      this.versions[versionId].processes = tmp;
    }, 50);
  };

  @action
  changeActiveVersion = (id: number | undefined) => {
    this.activeVersion = this.activeVersion === id ? undefined : id;
  };

  @action
  changeTopologyElements = (elements: TopologyElement[]) => {
    this.topologyElements = elements;
  };

  @action getAllModels = () => {
    // console.log('deviceTypes', JSON.stringify(toJS(this.deviceTypes)));
    // console.log('mediums', JSON.stringify(toJS(this.mediums)));
    // console.log('topology', JSON.stringify(toJS(this.topologyElements)));
    // console.log('versions', JSON.stringify(toJS(this.versions)));

    return {
      deviceTypes: toJS(this.deviceTypes),
      mediums: toJS(this.mediums),
      topology: toJS(this.topologyElements),
      versions: toJS(this.versions),
    };
  };

  checkDeviceTypeNameUniqueness = (name: string) => {
    const id = this.deviceTypes.findIndex((v) => v.name === name);
    return id === -1 || id === this.activeDeviceType;
  };

  checkMediumNameUniqueness = (name: string) => {
    const id = this.mediums.findIndex((v) => v.name === name);
    return id === -1 || id === this.activeMedium;
  };

  checkChannelNameUniqueness = (name: string, currentChannelId: number) => {
    let state = true;
    this.mediums.forEach((v) => {
      const valid =
        v.channels.findIndex(
          (v, index) => v.name === name && index !== currentChannelId
        ) === -1;

      if (!valid) {
        state = false;
      }
    });
    return state;
  };

  checkVersionNameUniqueness = (name: string) => {
    const id = this.versions.findIndex((v) => v.name === name);
    return id === -1 || id === this.activeVersion;
  };

  checkProcessNameUniqueness = (name: string, currentProcessId: number) => {
    let state = true;
    this.versions.forEach((v) => {
      const valid =
        v.processes.findIndex(
          (v, index) => v.name === name && index !== currentProcessId
        ) === -1;
      if (!valid) {
        state = false;
      }
    });
    return state;
  };
}

export default SimulationsStore;
