import { NodeModel } from '@projectstorm/react-diagrams';
import { BaseModelOptions } from '@projectstorm/react-canvas-core';
import { DeviceType } from '../../../models/DeviceType.model';
import { Channel } from '../../../models/Medium.model';
import { AdvancedPortModel } from '../CustomLinkModel';

export interface NodeModelOptions extends BaseModelOptions {
  deviceType: DeviceType;
  channels: Channel[];
}

export class CustomNodeModel extends NodeModel {
  deviceType: DeviceType;
  channels: Channel[];
  deviceId: number;

  static deviceCounter = 0;

  constructor(options: NodeModelOptions) {
    super({
      ...options,
      type: 'ts-custom-node',
    });

    this.deviceType = options.deviceType;
    this.channels = options.channels;

    CustomNodeModel.deviceCounter += 1;
    this.deviceId = CustomNodeModel.deviceCounter;

    this.channels &&
      this.channels.forEach((v, index) => {
        this.addPort(
          new AdvancedPortModel({
            in: true,
            name: this.deviceId + '^' + v.name + '^In' + index,
            channelName: v.name,
          })
        );
        this.addPort(
          new AdvancedPortModel({
            in: false,
            name: this.deviceId + '^' + v.name + '^Out' + index,
            channelName: v.name,
          })
        );
      });

    this.serialize();
  }

  serialize() {
    return {
      ...super.serialize(),
      deviceType: this.deviceType,
      channels: this.channels,
      deviceId: this.deviceId,
    };
  }

  deserialize(event: any): void {
    super.deserialize(event);
    this.deviceType = event.data.deviceType;
    this.channels = event.data.channels;
    this.deviceId = event.data.deviceId;
    CustomNodeModel.deviceCounter += 1;
  }
}
