import React from 'react';
import {
  IoIosDesktop,
  IoIosPlay,
  IoIosGitNetwork,
  IoIosWifi,
  IoIosList,
  IoIosCheckmarkCircle,
} from 'react-icons/io';
import TranslationService from '../services/TranslationService';

interface IProps {
  position: number;
  changePosition: (num: number) => void;
}

const ListElements = [
  {
    position: 0,
    Icon: IoIosDesktop,
  },
  {
    position: 1,
    Icon: IoIosWifi,
  },
  {
    position: 2,
    Icon: IoIosGitNetwork,
  },
  {
    position: 3,
    Icon: IoIosList,
  },
  {
    position: 4,
    Icon: IoIosPlay,
  },
];

class SimulationBottomBar extends React.Component<IProps> {
  render() {
    return (
      <div className="simulation-bottom-bar-outside">
        <div className="simulation-bottom-bar">
          <div className="simulation-bar-button-container">
            {this.props.position > 0 && (
              <div
                onClick={() =>
                  this.props.changePosition(this.props.position - 1)
                }
                className="round-button round-button-secondary"
              >
                <p>{TranslationService.redo_stop}</p>
              </div>
            )}
          </div>

          <div className="simulation-bottom-bar-icon-container">
            {ListElements.map((element) => (
              <div>
                <element.Icon
                  style={{ marginLeft: 10, marginRight: 10 }}
                  color={
                    element.position === this.props.position ? '#264766' : 'gray'
                  }
                />
                {element.position < this.props.position && (
                  <div className="simulation-bottom-bar-done-step">
                    <IoIosCheckmarkCircle />
                  </div>
                )}
              </div>
            ))}
          </div>

          <div className="simulation-bar-button-container">
            {this.props.position < 4 && (
              <div
                onClick={() =>
                  this.props.changePosition(this.props.position + 1)
                }
                className="round-button round-button-primary"
              >
                <p>{TranslationService.next_step}</p>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default SimulationBottomBar;
