import React from 'react';
//styles
import './Simulation.css';
//mobx
import { inject, observer } from 'mobx-react';
import RootStore from '../../stores/RootStore';
import { withRouter } from 'react-router-dom';
//components
import SimulationBottomBar from '../../components/SimulationBottomBar';
import DevicesTypes from './DevicesTypes';
import MediumChannels from './MediumChannels';
import Topology from './Topology';
import Versions from './Versions';
import Start from './Start';
import Modal from '../../components/modal/Modal';
import TranslationService from '../../services/TranslationService';
import { SimulationTemplate } from '../../models/SimulationTemplate.model';
import Simulation1 from '../../models/templates/Simulation1';
import SimulationTemplateModal from '../../components/simulation-template-modal/SimulationTemplateModal';

interface IProps {
  store?: RootStore;
  navigation?: any;
  history: any;
  location: any;
  match: any;
}

interface IState {
  mainPosition: number;
  showTemplateModal: boolean;
  loading: boolean;
  showModal: boolean;
  modalContent: {
    header: string;
    text: string;
    leftButtonLabel: string;
    rightButtonLabel?: string;
    onLeftClick: () => void;
    onRightClick?: () => void;
  };
  defaultSimulation?: SimulationTemplate;
}

@inject('store')
@observer
class SimulationPage extends React.Component<IProps, IState> {
  deviceTypesRef = React.createRef<DevicesTypes>();
  mediumsChannelsRef = React.createRef<MediumChannels>();
  topologyRef = React.createRef<Topology>();
  versionsRef = React.createRef<Versions>();

  state: IState = {
    mainPosition: -1,
    showTemplateModal: true,
    loading: true,
    showModal: false,
    modalContent: {
      header: '',
      text: '',
      leftButtonLabel: '',
      rightButtonLabel: undefined,
      onLeftClick: () => {},
      onRightClick: undefined,
    },
  };

  componentDidMount = () => {
    this.props.store?.resetNewSimulationStore();
    setTimeout(() => {
      this.setState({ loading: false });
    }, 100);
  };

  closeTemplateModal = () => {
    this.setState({ showTemplateModal: false, mainPosition: 0 });
  };

  setSimulation = (simulation: SimulationTemplate) => {
    this.setState({ defaultSimulation: simulation });
    this.props.store?.setSimulationModel(simulation);
    this.closeTemplateModal();
  };

  elements: {
    [key in string]: {
      checkFormValidation?: () => boolean;
      isArrayEmpty?: () => boolean;
      previousCallback?: () => void;
      nextCallback?: () => void;
    };
  } = {
    '0': {
      checkFormValidation: () => this.deviceTypesRef.current?.saveData()!,
      isArrayEmpty: () =>
        this.props.store?.newSimulationStore.deviceTypes.length === 0,
      previousCallback: () =>
        this.props.store?.newSimulationStore.resetDeviceTypes,
      nextCallback: () =>
        this.props.store?.newSimulationStore.changeActiveDeviceType(undefined),
    },
    '1': {
      checkFormValidation: () => this.mediumsChannelsRef.current?.saveData()!,
      isArrayEmpty: () =>
        this.props.store?.newSimulationStore.mediums.length === 0,
      previousCallback: () => this.props.store?.newSimulationStore.resetMediums,
      nextCallback: () => {
        this.props.store?.newSimulationStore.resetTopology();
        this.props.store?.newSimulationStore.changeActiveMedium(undefined);
        this.topologyRef.current?.resetApp();
      },
    },
    '2': {
      checkFormValidation: () => true,
      isArrayEmpty: () =>
        this.props.store?.newSimulationStore.topologyElements.length === 0,
      previousCallback: () => {
        this.props.store?.newSimulationStore.resetTopology();
        this.topologyRef.current?.resetApp();
      },
      nextCallback: () => {
        this.topologyRef.current?.serialize();
      },
    },
    '3': {
      checkFormValidation: () => this.versionsRef.current?.saveData()!,
      isArrayEmpty: () =>
        this.props.store?.newSimulationStore.versions.length === 0,
      previousCallback: () =>
        this.props.store?.newSimulationStore.resetVersions,
      nextCallback: () => {},
      // nextCallback: () =>
      //   this.props.store?.newSimulationStore.changeActiveVersion(undefined),
    },
    '4': {},
  };

  changePosition = (num: number) => {
    const element = this.elements[this.state.mainPosition.toString()];

    if (num < this.state.mainPosition) {
      this.setState({
        showModal: true,
        modalContent: {
          header: TranslationService.confirm,
          text: TranslationService.go_back_are_you_shure,
          leftButtonLabel: TranslationService.cancel,
          rightButtonLabel: TranslationService.accept,
          onLeftClick: () => this.setState({ showModal: false }),
          onRightClick: () => {
            element.previousCallback && element.previousCallback();
            this.setState({ mainPosition: num, showModal: false });
          },
        },
      });
    } else {
      if (element.checkFormValidation && !element.checkFormValidation()) {
        this.setState({
          showModal: true,
          modalContent: {
            header: TranslationService.warning,
            text: TranslationService.problems_in_form,
            leftButtonLabel: TranslationService.ok,
            onLeftClick: () => this.setState({ showModal: false }),
          },
        });
        return;
      }

      element.nextCallback && element.nextCallback();

      if (element.isArrayEmpty && element.isArrayEmpty()) {
        this.setState({
          showModal: true,
          modalContent: {
            header: TranslationService.warning,
            text: TranslationService.add_element_in_page,
            leftButtonLabel: TranslationService.ok,
            onLeftClick: () => this.setState({ showModal: false }),
          },
        });
        return;
      }

      this.setState({ mainPosition: num });
    }
  };

  render() {
    const {
      mainPosition,
      loading,
      modalContent,
      showModal,
      showTemplateModal,
    } = this.state;

    if (loading) {
      return null;
    }

    return (
      <div className="simulation-padding-container">
        {mainPosition === 0 && <DevicesTypes ref={this.deviceTypesRef} />}

        {mainPosition === 1 && <MediumChannels ref={this.mediumsChannelsRef} />}

        {mainPosition === 2 && (
          <Topology
            ref={this.topologyRef}
            defaultModel={this.state.defaultSimulation}
          />
        )}

        {mainPosition === 3 && <Versions ref={this.versionsRef} />}

        {mainPosition === 4 && <Start />}

        <SimulationBottomBar
          position={mainPosition}
          changePosition={this.changePosition}
        />

        {showModal && (
          <Modal
            text={modalContent.text}
            header={modalContent.header}
            leftButtonLabel={modalContent.leftButtonLabel}
            rightButtonLabel={modalContent.rightButtonLabel}
            onPressLeft={modalContent.onLeftClick}
            onPressRight={modalContent.onRightClick}
          />
        )}

        {showTemplateModal && (
          <SimulationTemplateModal
            emptySimulationPressed={this.closeTemplateModal}
            templateSimulationPressed={(item) => this.setSimulation(item)}
          />
        )}
      </div>
    );
  }
}

export default withRouter(SimulationPage);
