import UserStore from './UserStore';
import SimulationsStore from './SimulationsStore';
import NewSimulationStore from './NewSimulationStore';
import { SimulationTemplate } from '../models/SimulationTemplate.model';

export class RootStore {
  userStore: UserStore;
  simulationsStore: SimulationsStore;
  newSimulationStore: NewSimulationStore;

  constructor() {
    this.userStore = new UserStore(this);
    this.simulationsStore = new SimulationsStore(this);
    this.newSimulationStore = new NewSimulationStore(this);
  }

  resetNewSimulationStore = () => {
    this.newSimulationStore = new NewSimulationStore(this);
  };

  setSimulationModel = (simulation: SimulationTemplate) => {
    this.newSimulationStore.setDeviceTypes(JSON.parse(simulation.deviceTypes));
    this.newSimulationStore.setMediums(JSON.parse(simulation.mediums));
    this.newSimulationStore.setTopologyElements(
      JSON.parse(simulation.topology)
    );
    this.newSimulationStore.setVersions(JSON.parse(simulation.versions));
  };
}

export default RootStore;
